@import url(https://fonts.googleapis.com/css?family=Raleway:300);
@import url(https://fonts.googleapis.com/css?family=Lusitana:400,700);


@mixin clearfix {
   &:after {
      content: "";
      display: table;
      clear: both;
  }
}

.align-centerw {
   text-align: center;
}


.roww {
   @include clearfix;
   margin: -20px 0;
   .col {
      padding: 0 20px;
      float: left;
      box-sizing: border-box;
      &.x-50 {
         width: 50%;
      }
      &.x-100 {
         width: 100%;
      }
   }
}

.content-wrapper {
   min-height: 100%;
   position: relative;
}

.get-in-touchw {
   max-width: 650px;
   margin: 10rem auto;
   margin-top: 25rem;
   margin-bottom: 35rem;
   position: relative;
   top: 50%;
   transform: translateY(-50%);
  
  .title {
     text-align: center;
     font-family: Raleway, sans-serif;
     text-transform: uppercase;
     letter-spacing: 3px;
     font-size: 36px;
     line-height: 48px;
     padding-bottom: 48px;
  }
}

.contact-formw {
   .form-field {
      position: relative;
      margin: 32px 0;
   }
   .input-text {
      display: block;
      width: 100%;
      height: 36px;
      border-width: 0 0 2px 0;
      border-color: var(--borderc);
      color: var(--borderc);
      background-color: var(--backc);
      font-family: Lusitana, serif;
      font-size: 18px;
      line-height: 26px;
      font-weight: 400;
      
      &:focus {
         outline: none;
      }
      
      &:focus,
      &.not-empty {
         + .label {
            transform: translateY(-24px);
         }
      }
   }
   .input-area {
    display: block;
    width: 100%;
    height: 8rem;
    border-width: 0 0 2px 0;
    color: var(--borderc);
    background-color: var(--backc);
    border-color: var(--borderc);
    font-family: Lusitana, serif;
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
    
    &:focus {
       outline: none;
    }
    
    &:focus,
    &.not-empty {
       + .label2 {
          transform: translateY(-24px);
       }
    }
 }
   .label {
      position: absolute;
      left: 20px;
      bottom: 11px;
      font-family: Lusitana, serif;
      font-size: 18px;
      line-height: 26px;
      font-weight: 400;
      color: var(--labelc);
      cursor: text;
      transition: transform .2s ease-in-out;
   }
   .label2 {
    position: absolute;
    left: 20px;
    top: 0px;
    font-family: Lusitana, serif;
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
    color: var(--labelc);
    cursor: text;
    transition: transform .2s ease-in-out;
 }
   
   .submit-btn {
      display: inline-block;
      background-color: var(--borderc);
      color: var(--fontc);
      font-family: Raleway, sans-serif;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-size: 16px;
      line-height: 24px;
      padding: 8px 16px;
      border: none;
      cursor: pointer;
   }
}

@media screen and (max-width: 580px){
	.get-in-touchw  {
		margin-top: 14rem;
        margin-bottom: 35rem;
	}
  }


